import Bugsnag from '@bugsnag/web-worker'
import { Client, Event } from '@bugsnag/core'
import { serialize } from '../utils/serialize'
import { API } from './API'
import Browser from 'webextension-polyfill'

declare module '~/core/API' {
  interface API {
    Bugsnag: ReturnType<typeof BugsnagApi.initAPI>
  }
}

let api: API
const log = (
  funcName: string,
  message: string,
  payload: any,
  level: string,
) => {
  if (!MINIFIED) {
    // Can't use Logger here because it would cause a circular dependency
    // eslint-disable-next-line no-console
    console.log('Bugsnag.' + funcName, message, payload, level)
  }
}

// Some helper functions ported from sensortower/stayfree-desktop/src/plugins/bugsnag
function getValidStacktracePrefix() {
  const [, protocol, id] = /(.*?):\/\/(.*)/.exec(
    Browser.runtime.getURL('/'),
  ) ?? ['', '', '']
  return `${protocol.replace(/-/g, '_')}://${id}`
}

function isStringable(value: 'string' | 'boolean' | 'number') {
  return (
    typeof value === 'string' ||
    typeof value === 'boolean' ||
    typeof value === 'number'
  )
}

function isErrorFromExtension(error: any) {
  return !!error?.stacktrace?.find((frame: any) =>
    frame.file.startsWith(VALID_STACKTRACE_PREFIX),
  )
}

function isErrorIgnored(error: any, ignoreMatches: string[]) {
  return !!ignoreMatches.find((ignored) => error.errorMessage.match(ignored))
}

function fixStackTrace(event: any) {
  event.errors.forEach((err: any) => {
    err.stacktrace = err.stacktrace.map((frame: any) => {
      frame.file = frame.file.replace(/chrome-extension:/g, 'chrome_extension:')
      frame.file = frame.file.replace(/moz-extension:/g, 'moz_extension:')
      frame.file = frame.file.replace(/safari-extension:/g, 'safari_extension:')
      frame.file = frame.file.replace(
        /safari-web-extension:/g,
        'safari_web_extension:',
      )

      return frame
    })
  })
}

function getErrorFromArgs(...args: any[]) {
  return args.find((arg) => arg instanceof Error) ?? getStringFromArgs(args)
}

function getStringFromArgs(...args: any[]) {
  return args
    .map((arg) =>
      isStringable(arg) ? String(arg) : JSON.stringify(serialize(arg)),
    )
    .join(' ')
}

function excludeFuncFromStacktrace(func: Function) {
  return (event: any) => {
    event.errors.forEach((err: any) => {
      const sliceAt =
        1 + err.stacktrace.findIndex((trace: any) => trace.method === func.name)
      err.stacktrace = err.stacktrace.slice(sliceAt)
    })
  }
}

let VALID_STACKTRACE_PREFIX = ''

const BugsnagApi = {
  bugsnagClient: null as null | Client,
  apiKey: '14be373dfe46ae64888094a22d25ce46',
  sampleGroup: 0, // Number from 0-100, used to decide if an event should be sampled.
  initAPI(theAPI: API) {
    api = theAPI
    return api.mixin('Bugsnag', {
      init: this.init.bind(this),
    })
  },
  async init(context: any) {
    VALID_STACKTRACE_PREFIX = getValidStacktracePrefix()
    if (this.bugsnagClient != null) {
      log('init', 'Bugsnag was already initialized', {}, 'DEBUG')
      return
    }

    await Promise.all([api.Requirements.init()])
    const installId = await api.Storage.get('install-id')
    const hasAcceptedPrivacyPolicy = api.Settings.get(
      'hasAcceptedPrivacyPolicy',
    )
    const hasCompletedOnboarding =
      await api.Requirements.isOnboardingCompleted()
    const isNewUser = api.Settings.get('isNewUser')
    this.bugsnagClient = Bugsnag.start({
      apiKey: this.apiKey,
      appType: 'Web Extension',
      collectUserIp: false,
      appVersion: api.Chrome.Extension.getVersion(),
      enabledReleaseStages: ['production'],
      releaseStage: MINIFIED ? 'production' : 'development',
      context: context ?? 'unknown',
      logger: null,
      generateAnonymousId: false,
      onError: [fixStackTrace, this.isValidError.bind(this)],
      onSession: [this.isValidSession.bind(this)],
    })

    this.bugsnagClient.setUser(installId)

    this.bugsnagClient.addMetadata('user properties', {
      hasAcceptedPrivacyPolicy,
      hasCompletedOnboarding,
      isNewUser,
    })

    this.sampleGroup = await api.Storage.getOrSet(
      'bugsnagSamplingGroup',
      100 * Math.random(),
    )

    log('init', 'Initialized Bugsnag for ' + context, this, 'INFO')
  },
  notifyError(...args: any[]) {
    this.bugsnagClient?.notify(getErrorFromArgs(args), (event) => {
      excludeFuncFromStacktrace(this.notifyError)(event)
      event.severity = 'error'
    })
  },
  notifyWarning(...args: any[]) {
    this.bugsnagClient?.notify(getErrorFromArgs(args), (event) => {
      excludeFuncFromStacktrace(this.notifyWarning)(event)
      event.severity = 'warning'
    })
  },
  logBreadcrumb(...args: any[]) {
    this.bugsnagClient?.leaveBreadcrumb(
      typeof args[0] === 'string' ? args[0] : '(no message)',
      { args: serialize(args) },
      'log',
    )
  },
  disabled() {
    return !this.isReady() || !this.canUploadData()
  },
  /**
   * Returning false will prevent the error event from being uploaded.
   */
  async isValidError(event: Event) {
    if (this.disabled()) return false
    if (!this.isSampled()) return false

    const bugsnagIgnoreMatches = api.RemoteConfig.get('bugsnagIgnoreMatches')
    if (!event.errors) return false

    return !!event.errors.find(
      (error: any) =>
        isErrorFromExtension(error) &&
        !isErrorIgnored(error, bugsnagIgnoreMatches),
    )
  },
  /**
   * Returning false will prevent the session from being uploaded.
   */
  isValidSession() {
    if (this.disabled()) return false
  },
  /**
   * @returns true if the user has opted into data collection.
   */
  canUploadData() {
    return api.Settings.get('hasAcceptedPrivacyPolicy') ?? false
  },
  /**
   * @returns true when bugsnag and it's dependencies are ready to be used.
   */
  isReady() {
    return api.Settings.isInitialized()
  },
  /**
   * @returns true if the event should be uploaded.
   */
  isSampled() {
    const defaultSampling = api.RemoteConfig.get('bugsnagSampling')
    return this.sampleGroup < defaultSampling
  },
}

export default BugsnagApi
