import { createPanelApiClient } from '@sensortower/panel-api'
import { defineProxyService } from '@webext-core/proxy-service'

export const [registerBackroundApi, injectBackgroundApi] = defineProxyService(
  'BackgroundApi',
  () => {
    return createPanelApiClient({
      ofetchDefaults: {
        baseURL: 'https://stayfocusd.st-panel-api.com',
      },
    })
  },
)
