// Don't change the order of these imports.
// Not sure why but it creates broken output in dist folder and
// chrome complains with `Uncaught SyntaxError: Illegal return statement`
import * as AdFinder from '@sensortower/ad-finder'
import API from '~/core/CoreAPI'
import Logger from '~/core/Logger'
import { injectBackgroundApi } from '~/utils/backround-api'
import { initPubSubForwarder } from '~/utils/initPubSubForwarder'
import AppConst from './AppConsts'

let currentTabUrl: string

const port = chrome.runtime.connect({ name: 'ad-finder' })
initPubSubForwarder()

API.PubSub.listen(
  'StayFocusd.tab.selected.*',
  function (message: string, payload: any) {
    currentTabUrl = payload.tab.url
  },
)

const init = async () => {
  const installId = await API.Storage.get('install-id')

  AdFinder.initAdFinderInContentScript(
    {
      getInstallId: () => API.Storage.get('install-id', true),
      sendMessageToBackground: (message) => {
        // Used to ensure we wait for a response from this specific request, not another, similar request.
        const msgNonce = Math.random()
        port.postMessage({
          message: 'forward-message.request',
          payload: message,
          nonce: msgNonce,
        })
        return new Promise((res) => {
          const listener = ({
            message,
            payload,
            nonce,
          }: {
            message: string
            payload: any
            nonce: number
          }) => {
            if (message === 'forward-message.response' && msgNonce === nonce) {
              port.onMessage.removeListener(listener)
              res(payload)
            }
          }
          port.onMessage.addListener(listener)
        })
      },
      logger: MINIFIED ? null : console,
      enablePathmaticsLogs: !MINIFIED,
      mv3PathmaticsPath: 'common/youtube-hulu-vast-ads.js',
      onAdDetected: (ad) => {
        port.postMessage({
          message: 'ad-found',
          payload: {
            hostname: currentTabUrl,
            ad: ad,
          },
        })
      },
    },
    {
      api: injectBackgroundApi(),
      appId: AppConst.APP_ID,
      installId: installId,
      instacartSelectors: await API.RemoteConfig.get('instacartSelectors'),
      onAdFound(_ad, container) {
        // Highlight the elements during development
        if (import.meta.env.DEV) {
          container.style.backgroundColor = 'red'
          container.style.outline = '8px solid red'
        }
      },
    },
  )
}

;(async () => {
  await API.Storage.initAsync()
  await API.RemoteConfig.init()
  const isEnabled = await API.IncrementalRollout.AdFinder.isEnabled()

  Logger.log('ad-finder', 'Ad finder enabled? ' + isEnabled, undefined, 'INFO')
  if (isEnabled) init()
})()
