import API from '~/core/CoreAPI'
import Logger from '~/core/Logger'

const extensionID = API.Chrome.Extension.getID()
export function initPubSubForwarder() {
  /* Listen for requests coming from background scripts and republish via PubSub */
  API.Chrome.Message.on.addListener(function (request, sender, callback) {
    request.payload = request.payload || {}
    request.payload.sender = sender || {}

    /* If the message isn't coming from a tab, republish it */
    if (!sender.tab) {
      API.PubSub.publish({
        message: request.message,
        payload: request.payload,
        onPublish: typeof callback === 'function' ? callback : function () {},
      })
    }

    return true
  })

  /* Subscribe to all messages and pipe them through the Chrome.Message API */
  API.PubSub.subscribe({
    async: true,

    handler: function (message, payload, callback) {
      payload = payload || {}

      function callbackWrapper(callback) {
        if (chrome.runtime.lastError) {
          Logger.log(
            'Content.js subscribe to all messages',
            'runtime.lastError',
            chrome.runtime.lastError,
          )
        }

        if (callback != undefined && typeof callback === 'function') {
          return () => callback
        } else {
          return () => true
        }
      }

      callback = callbackWrapper(callback)

      /* If the message is not coming from the extension, send it */
      if (!payload.sender || payload.sender.id !== extensionID) {
        API.Chrome.Message.send(
          null,
          { message: message.toString(), payload: payload },
          null,
          callback,
        )
      }

      return true
    },
  })
}
