import Bugsnag from '../core/Bugsnag'

if (typeof MINIFIED === 'undefined') {
  MINIFIED = false
}

export type LogLevel = 'ERROR' | 'WARN' | 'INFO' | 'DEBUG' | 'TRACE'

/* eslint-disable no-console */
function createLogger() {
  /**
   * Default log() implementation
   *
   * This can be overridden by defining a log() function in the initObj
   * passed to the constructor
   *
   * @param funcName The name of the function generating the log message
   * @param message The message to log
   * @param payload Data object
   * @param _level
   */
  const log = function (...args: [string, string, any, LogLevel | undefined]) {
    const [funcName, _message, _payload, _level] = args

    const payload = !_payload ? '' : _payload
    const level = _level ?? 'INFO'
    const message = '[' + funcName + '()] ' + _message

    if (level === 'ERROR') {
      if (!MINIFIED) {
        console.error(message, payload)
      }
      Bugsnag.notifyError(...args)
    } else if (level === 'WARN') {
      if (!MINIFIED) {
        console.warn(message, payload)
      }
      Bugsnag.logBreadcrumb(...args)
      Bugsnag.notifyWarning(...args)
    } else if (level === 'INFO') {
      if (!MINIFIED) {
        console.info(message, payload)
      }
      Bugsnag.logBreadcrumb(...args)
    } else {
      if (!MINIFIED) {
        console.log(message, payload)
      }
      Bugsnag.logBreadcrumb(...args)
    }
  }

  return {
    log: log,
  }
}

const Logger = createLogger()
export default Logger
