/**
 * Ported from sensortower/stayfree-desktop/src/utils/log.js
 * `JSON.stringify` doesn't properly stringify all objects, like errors, so this function prepares
 * an object by recursively making special objects into regular objects so it contains details after
 * `JSON.stringify`ing it.
 */
export function serialize(object) {
  if (object == null) {
    return object
  }
  if (object instanceof Error) {
    return { errorClass: object.name, errorMessage: object.message }
  }
  if (typeof object === 'function') {
    return '[Function]'
  }
  if (Array.isArray(object)) {
    return object.map(serialize)
  }
  if (typeof object === 'object') {
    return Object.entries(object).reduce((serialized, [key, value]) => {
      serialized[key] = serialize(value)
      return serialized
    }, {})
  }
  return object
}
